.login h1 {
  margin-top: 20px;
  width: 100%;
  text-align: center; }

.login .register {
  width: 100%;
  text-align: center;
  font-size: 1em; }
  .login .register a {
    font-style: italic;
    text-decoration: none; }

.login .content {
  padding-top: 45px;
  width: 300px;
  height: auto;
  margin: auto; }
  .login .content .password {
    margin: 25px 0px; }
  .login .content .forgot-password {
    width: 100%;
    padding-left: 15px; }
    .login .content .forgot-password a {
      font-style: italic;
      text-decoration: none; }
  .login .content input {
    border-color: #888; }

.login .action {
  padding-top: 20px;
  width: 100%;
  text-align: center; }
  .login .action button {
    padding: 0 25px; }



