.homePage .header-content {
  width: 100%;
  height: 100%;
  display: block; }
  .homePage .header-content .logo {
    position: relative;
    float: left;
    height: 100%;
    color: white;
    font-weight: bold;
    font-size: 2em; }
    .homePage .header-content .logo span {
      position: absolute; }
    .homePage .header-content .logo .logo-spare {
      position: absolute;
      display: none; }
  .homePage .header-content .header-icon {
    float: right;
    height: 100%;
    color: white;
    font-weight: bold;
    font-size: 1.5em; }

.homePage .ant-layout-sider-trigger {
  background-color: #e9e9e9;
  color: black;
  font-weight: bold;
  font-size: 1.2em; }

.homePage .ant-menu-item-divider {
  background-color: #c6c6c6; }

@media screen and (max-width: 767px) {
  .homePage .ant-layout-header {
    padding: 0 5px; }
  .homePage .header-content .logo span {
    display: none; }
  .homePage .header-content .logo .logo-spare {
    display: block; } }



